import { Injectable } from '@angular/core';
import {HttpService} from '../service/http.service';
import {Syllabus} from '../models/syllabus';
import {AlertService} from '../alert/alert.service';

@Injectable()
export class SyllabusService {

  constructor(
    private http: HttpService,
    private alertService: AlertService
  ) { }

  fetchSyllabusList(): Promise<any> {
    return this.http.get('course').then( ( data ) => {
      if ( data.success ) {
        return data.data;
      }
      return [];
    } );
  }

  removeSyllabus(id: string): Promise<any> {
    return this.http.remove( `course/${id}`).then( data => {
      if ( data.success ) {
        this.alertService.alert({
          type: 'success',
          title: '??????',
          content: '??????????????????'
        });
      } else {
        this.alertService.alert({
          type: 'danger',
          title: '??????',
          content: '??????????????????'
        });
        throw new Error('??????????????????');
      }
    } );
  }

  newSyllabus(syllabus: Syllabus): Promise<any> {
    return this.http.post( 'course', syllabus).then( data => {
      if (data.success) {
        this.alertService.alert({
          title: '??????',
          content: '??????????????????',
          type: 'success'
        });
        return data.data.id;
      } else {
        this.alertService.alert({
          title: '??????',
          content: '??????????????????????????????',
          type: 'danger'
        });
        throw new Error('??????????????????');
      }
    } );
  }

  updateSyllabus(syllabus: Syllabus) {
    return this.http.put('course', syllabus).then( data => {
      if (data.success) {
        this.alertService.alert({
          type: 'success',
          title: '??????',
          content: '??????????????????',
        });
      } else {
        this.alertService.alert({
          type: 'danger',
          title: '??????',
          content: data.data,
        });
        throw new Error('??????????????????');
      }
    } );
  }

}



