import { Injectable } from '@angular/core';
import {HttpService} from '../service/http.service';
import {AlertService} from '../alert/alert.service';

@Injectable()
export class AdminService {

  constructor(
    private http: HttpService,
    private alertService: AlertService
  ) { }

  fetchUserList(): Promise<any> {
    return this.http.get('admin/user').then( result => {
      if (result.success) {
        return result.data;
      } else {
        this.alertService.alert({
          title: '??????',
          content: '????????????????????????',
          type: 'danger'
        });
        return [];
      }
    } );
  }

  setNewPassword(body): Promise<any> {
    return this.http.put('admin/user/pwd', body).then( data => {
      if (data.success) {
        this.alertService.alert({
          title: '??????',
          content: '??????????????????',
          type: 'success'
        });
        return data.data;
      } else {
        this.alertService.alert({
          title: '??????',
          content: '??????????????????',
          type: 'danger'
        });
      }
    } );
  }

  setRoleType(body): Promise<any> {
    return this.http.put('admin/user/role', body).then( data => {
      console.log(data);
      if (data.success) {
        this.alertService.alert({
          title: '??????',
          content: '?????????????????????',
          type: 'success'
        });
      } else {
        this.alertService.alert({
          title: '??????',
          content: '?????????????????????',
          type: 'danger'
        });
      }
      return data.data;
    } );
  }

  updateSchoolInfo(body: object): Promise<any> {
    return this.http.put('admin/school/', body).then( data => {
      if (data.success) {
        this.alertService.alert({
          title: '??????',
          content: '?????????????????????',
          type: 'success'
        });
        return data.data;
      } else {
        this.alertService.alert({
          title: '??????',
          content: '????????????????????????',
          type: 'danger'
        });
        throw Error('????????????????????????');
      }
    } );
  }

  addSchool(body): Promise<any> {
    return this.http.post('admin/school', body).then( data => {
      if (data.success) {
        this.alertService.alert({
          title: '??????',
          content: '?????????????????????' + body.name,
          type: 'success'
        });
        data.data.createTime = Date.now();
        return data.data;
      } else {
        this.alertService.alert({
          title: '??????',
          content: '????????????????????????',
          type: 'danger'
        });
        throw Error('????????????????????????');
      }
    } );
  }
}



